@import "../../styles/index";

.Button {
  @include borders;
  @include button;
  align-items: center;
  display: flex;
  font-weight: bold;
  height: 24px;
  justify-content: center;
  overflow: hidden;
  width: 24px;
  &.visible {
    border-color: #7b7b7b;
    border-width: 1px;
  }
  &.red {
    background: red;
  }
  img {
    height: 20px;
    width: 20px;
  }
  &.value-1 {
    color: blue;
  }
  &.value-2 {
    color: green;
  }
  &.value-3 {
    color: red;
  }
  &.value-4 {
    color: purple;
  }
  &.value-5 {
    color: maroon;
  }
  &.value-6 {
    color: turquoise;
  }
  &.value-7 {
    color: black;
  }
  &.value-8 {
    color: gray;
  }
}
